'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './contacto.routes';

export class ContactoComponent {
  /*@ngInject*/
  constructor(seoService) {
    this.createMetadata(seoService);    
  }

  createMetadata(seoService){
    seoService.title('Contacto | PAD');
    seoService.description('El Programa de Alfabetizacin Digital es la propuesta pedaggica de la DTE para acompaar a los docentes en la enseanza TIC, destinada a las escuelas primarias y CEC de la Provincia de Buenos Aires.');
    seoService.keyboards(['pad', 'buenos aires', 'ba', 'educacion' , 'programa provincial', 'alfabetizacin digital', 'escritorio digital']);
  }
}

export default angular.module('padApp.contacto', [uiRouter])
  .config(routes)
  .component('contacto', {
    template: require('./contacto.html'),
    controller: ContactoComponent,
    controllerAs: 'contactoCtrl'
  })
  .name;



