        _blockAlias['Produccin (Ed.Ar. - Msica - Materiales)'] = 'Produccin';
        _blockAlias['Recepcin (Ed.Ar. - Msica - Materiales)'] = 'Recepcin';

        _blockAlias['Contexto sociocultural (Ed.Ar. - Msica - Lenguaje)'] = 'Contexto sociocultural';
        _blockAlias['Lenguaje (Ed.Ar. - Msica - Lenguaje)'] = 'Lenguaje';
        _blockAlias['Produccin (Ed.Ar. - Msica - Lenguaje)'] = 'Produccin';
        _blockAlias['Recepcin (Ed.Ar. - Msica - Lenguaje)'] = 'Recepcin';
        
        _blockAlias['Contexto sociocultural (Ed.Ar. - Msica - Composicin)'] = 'Contexto sociocultural';
        _blockAlias['Lenguaje (Ed.Ar. - Msica - Composicin)'] = 'Lenguaje';
        _blockAlias['Produccin (Ed.Ar. - Msica - Composicin)'] = 'Produccin';
        _blockAlias['Recepcin (Ed.Ar. - Msica - Composicin)'] = 'Recepcin';

        _blockAlias['Contexto sociocultural (Ed.Ar. - Danza - El cuerpo)'] = 'Contexto sociocultural';
        _blockAlias['Lenguaje (Ed.Ar. - Danza - El cuerpo)'] = 'Lenguaje';
        _blockAlias['Produccin (Ed.Ar. - Danza - El cuerpo)'] = 'Produccin';
        _blockAlias['Recepcin (Ed.Ar. - Danza - El cuerpo)'] = 'Recepcin';
        
        _blockAlias['Contexto sociocultural (Ed.Ar. - Danza - Lenguaje)'] = 'Contexto sociocultural';
