        _blockAlias['Produccin (Ed.Ar. - Danza - Discursos)'] = 'Produccin';
        _blockAlias['Recepcin (Ed.Ar. - Danza - Discursos)'] = 'Recepcin';

    var _inverseBlockAlias = {};
        _inverseBlockAlias['Ed. Artstica - MsicaSala de DocentesSin Especificar'] = 'Sin Especificar';
        _inverseBlockAlias['Ed. Artstica - MsicaComposicinProduccin'] = 'Produccin (Ed.Ar. - Msica - Composicin)';
        _inverseBlockAlias['Ed. Artstica - MsicaComposicinContexto sociocultural'] = 'Contexto sociocultural (Ed.Ar. - Msica - Composicin)';
        _inverseBlockAlias['Ed. Artstica - MsicaComposicinLenguaje'] = 'Lenguaje (Ed.Ar. - Msica - Composicin)';
